<template src="./App.template.pug" lang="pug"></template>

<script>
  export default {
    name: 'RadarApp',
    data () {
      return {
        isSidebarOpened: true,
        sidebarSections: [
          {
            title: 'Home'
          },
          {
            title: 'About'
          }
        ],
        mainSections: [
          {
            title: 'Galería',
            icon: '',
            path: '/gallery'
          },
          {
            title: 'Adoptar mascota',
            icon: '',
            path: '/adoptionForm'
          },
          {
            title: 'Reportar mascota perdida',
            icon: '',
            path: 'foundPetForm'
          },
          {
            title: 'Editar perfil de usuario',
            icon: '',
            path: '/editUserProfile'
          },
          {
            title: 'Equipo Radar',
            icon: '',
            path: '/teamView'
          },
          {
            title: 'Login',
            icon: '',
            path: '/login'
          }
        ]
      }
    },
    methods: {
      navigateTo: function (index) {
        console.log('navegate', index)
        this.$router.push(this.mainSections[index].path)
      }
    }
  }
</script>

<style>

  .with.top-toolbar main > .content {
    padding-top: 0;
  }

  .md-tabs .md-tab {
    padding: 0;
  }

  .not-elevation {
    z-index: 0
  }

</style>



